<template>
  <div>
    <i-form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100">
      <FormItem label="名字" prop="realName" @submit.native.prevent>
        <Input v-model="formValidate.realName" placeholder="填写名字" />
      </FormItem>
      <FormItem label="身份证" prop="idcard">
        <Input v-model="formValidate.idcard" placeholder="填写身份证" />
      </FormItem>
      <FormItem label="身份证正面" prop="identityFront">
        <Upload
          accept="image/*"
          :action="fileconfig.fileUrl"
          :headers="fileconfig.fileHead"
          name="file"
          :on-success="identityFrontfileSuccess"
          :show-upload-list="false"
        >
          <Button icon="ios-cloud-upload-outline">身份证正面</Button>
          <div v-if="formValidate.identityFront != ''">
            <p>{{formValidate.identityFront}}</p>
            <p style="color: #2868d1;">*已添加,想更换直接上传新身份证正面即可</p>
          </div>
        </Upload>
      </FormItem>
      <FormItem label="身份证反面" prop="identityBehind">
        <Upload
          accept="image/*"
          :action="fileconfig.fileUrl"
          :headers="fileconfig.fileHead"
          name="file"
          :on-success="identityBehindfileSuccess"
          :show-upload-list="false"
        >
          <Button icon="ios-cloud-upload-outline">身份证反面</Button>
          <div v-if="formValidate.identityBehind != ''">
            <p>{{formValidate.identityBehind}}</p>
            <p style="color: #2868d1;">*已添加,想更换直接上传新身份证反面即可</p>
          </div>
        </Upload>
      </FormItem>
      <div style="text-align: center;">
        <Button type="primary" @click="handleSubmit('formValidate')">提交个人认证</Button>
      </div>
    </i-form>
  </div>
</template>
<script>
import { fileUploadUrl, messages, validateNub } from "../../../utils";
import { personal } from "../../../api/user";
import { FormItem, Form, Input, Button, Upload } from "iview";
export default {
  components: {
    [Form.name]: Form,
    [FormItem.name]: FormItem,
    [Button.name]: Button,
    [Input.name]: Input,
    [Upload.name]: Upload
  },
  data() {
    return {
      // 文件上传配置
      fileconfig: {
        fileUrl: fileUploadUrl,
        fileHead: {
          token: document.cookie.match(
            new RegExp("(^| )" + "token" + "=([^;]*)(;|$)")
          )[2]
        }
      },
      //表单数据
      formValidate: {
        realName: "",
        idcard: "",
        identityFront: "",
        identityBehind: ""
      },
      //表单验证
      ruleValidate: {
        realName: [
          { required: true, message: "名字必填,不能为空", trigger: "blur" }
        ],
        idcard: [
          {
            required: true,
            validator: validateNub,
            message: "身份证号必填,必须为数字",
            trigger: "blur"
          }
        ],
        identityFront: [
          {
            required: true,
            message: "身份证正面必传,不能为空",
            trigger: "change"
          }
        ],
        identityBehind: [
          {
            required: true,
            message: "身份证反面必传,不能为空",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
    /**
     * @function 身份证正面上传
     */
    identityFrontfileSuccess(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.identityFront = res.data.url;
      }
    },
    /**
     * @function 身份证反面上传
     */
    identityBehindfileSuccess(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.identityBehind = res.data.url;
      }
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          personal(this.formValidate).then(res => {
            if (res.data.status == "SUCCESS") {
              messages.success(
                `个人信息提交成功，等待审核,将在1-7个工作日内审核`
              );
              this.$router.push("/user/certify-index");
            }
          });
        } else {
          messages.warning(`必填项不能为空,请认真填写`);
        }
      });
    }
  }
};
</script>