<template>
  <div class="bg">
    <div class="title">我的认证</div>
    <Alert class="alert" type="warning" show-icon v-if>
      {{userAuth == 0 ? '必须先进行个人认证,认证完成后可以进行企业/政府认证'
      : userAuth == 1 ? '您已经完成个人认证，如果想发布企业/政府项目请完善企业/政府认证'
      : userAuth == 2 ? '您已经完成个人和企业认证,您可以添加新的企业认证,也可以申请政府认证（提醒：政府认证通过,个人认证/企业认证将失效,只能发布政府项目/资金）' :'未知错误' }}
    </Alert>
    <br />
    <div>
      <div class="head">
        <p style="font-size: 18px;margin: 10px;font-weight: 600;">
          <span style="color:red;">*</span>请先选择认证类型
        </p>
        <br />
        <RadioGroup v-model="componentId" size="large" style="margin:0 auto;">
          <Radio label="personal" :disabled="userAuth != 0">个人认证</Radio>
          <Radio label="enterprise" :disabled="userAuth == 0">企业认证</Radio>
          <Radio label="government" :disabled="userAuth == 0">政府认证</Radio>
        </RadioGroup>
      </div>
      <div style="margin-top:20px;">
        <component :is="componentId"></component>
      </div>
    </div>
  </div>
</template>
<script>
import { RadioGroup, Radio, Alert } from "iview";
import Personal from "../../components/user/certify/personal";
import government from "../../components/user/certify/government";
import enterprise from "../../components/user/certify/enterprise";
import { getUserInfo } from "../../api/user";
import { messages } from "../../utils";
export default {
  components: {
    [Alert.name]: Alert,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    Personal,
    government,
    enterprise
  },
  data() {
    return {
      userAuth: null,
      componentId: null
    };
  },
  methods: {
    /**
     * @function 选项栏切换事件
     * @param {需要切换的组件名称} val
     */
    radioChange(val) {
      this.componentId = res;
    }
  },
  created() {
    /**
     * @function 获取用户基本信息，将对应选项栏切换，如果正在审核则跳出
     */
    getUserInfo().then(res => {
      this.userAuth = res.data.data.user.auditingType;
      //auditingNowStatus === 0 说明当前已经有在审核的认证
      if (res.data.data.user.auditingNowStatus == 0) {
        messages.error("已有认证正在申请,请耐心等待");
        this.$router.push("/user/certify-index");
      } else if (this.userAuth == 0) {
        this.componentId = "personal";
      } else if (this.userAuth == 1 || this.userAuth == 2) {
        //判断路由是否有带参数并且是否是government 是的话展示政府认证组件 其他情况暂时企业认证组件
        if (this.$route.query.status == "government") {
          this.componentId = "government";
        } else {
          this.componentId = "enterprise";
        }
      } else if (this.userAuth == 3) {
        //否则说明他已经是政府,政府不能在进行认证所以跳回
        messages.error("您已经是政府认证,不可继续认证");
        this.$router.push("/user/certify-index");
      }
    });
  }
};
</script>
