<template>
  <div>
    <i-form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="180">
      <form-item label="请输入单位名称" prop="unitsName" @submit.native.prevent>
        <Input v-model="formValidate.unitsName" placeholder="填写单位名称" />
      </form-item>
      <form-item label="请输入单位简介" prop="unitsIntroduction" @submit.native.prevent>
        <Input
          v-model="formValidate.unitsIntroduction"
          type="textarea"
          :rows="4"
          placeholder="填写单位简介"
        />
      </form-item>
      <form-item label="省/市/区" prop="address" @submit.native.prevent>
        <Cascader
          @on-change="address"
          :data="addList"
          style="width: 30%; float: left; margin-right:1%;"
        ></Cascader>
        <Input v-model="formValidate.detailedAddress" placeholder="详细地址" style="width: 69%;" />
      </form-item>
      <form-item label="单位法人证书 ( 照片 )" prop="unitsCertificate" @submit.native.prevent>
        <label>
          <Upload
            accept="image/*"
            :action="fileconfig.fileUrl"
            :headers="fileconfig.fileHead"
            name="file"
            :on-success="unitsCertificate"
            :show-upload-list="false"
          >
            <Button icon="ios-cloud-upload-outline">单位法人证书</Button>
            <div v-if="formValidate.unitsCertificate != ''">
              <p>{{formValidate.unitsCertificate}}</p>
              <p style="color: #2868d1;">*已添加,想更换直接上传新单位法人证书即可</p>
            </div>
          </Upload>
        </label>
      </form-item>
      <form-item label="单位授权书 ( PDF )" prop="unitsAuthorization" @submit.native.prevent>
        <Upload
          accept=".pdf"
          :action="fileconfig.fileUrl"
          :headers="fileconfig.fileHead"
          name="file"
          :on-success="unitsAuthorization"
          :show-upload-list="false"
        >
          <Button icon="ios-cloud-upload-outline">单位授权书</Button>
          <div v-if="formValidate.unitsAuthorization != ''">
            <p>{{formValidate.unitsAuthorization}}</p>
            <p style="color: #2868d1;">*已添加,想更换直接上传新单位授权书即可</p>
          </div>
        </Upload>
        <a :href="`${baseUrl}/authorization/LetterOfAuthorization.doc`" style="color:#2868d1;">&nbsp;&nbsp; *点击下载授权模板文件</a>
      </form-item>
      <form-item>
        <Button type="primary" @click="modalShow=true">提交政府认证</Button>
      </form-item>
    </i-form>
    <Modal v-model="modalShow" title="重要操作提醒" @on-ok="handleSubmit('formValidate')">
      <p>1、账号申请为政府主体，认证通过后不能发布个人项目及资金和企业项目及资金，仅能发政府资金或项目。</p>
      <p>2、账号主体不能认证更变为个人认证、企业认证；保证政府账号唯一性。</p>
      <p>3、更变为政府账号需将正在进行中的项目完成融资或主动冻结项目（确定后系统自动操作）；需冻结资金信息（系统自动操作）。</p>
      <p>4、您是否还要继续提交政府认证。</p>
    </Modal>
  </div>
</template>
<script>
import { fileUploadUrl, messages,baseUrl } from "../../../utils";
import { government } from "../../../api/user";
import { areasList } from "../../../areas-list";
import { FormItem, Form, Input, Button, Cascader, Upload, Modal } from "iview";
export default {
  components: {
    [Form.name]: Form,
    [FormItem.name]: FormItem,
    [Button.name]: Button,
    [Input.name]: Input,
    [Cascader.name]: Cascader,
    [Modal.name]: Modal,
    [Upload.name]: Upload
  },
  data() {
    return {
      baseUrl,
      modalShow: false,
      // 文件上传配置
      fileconfig: {
        fileUrl: fileUploadUrl,
        fileHead: {
          token: document.cookie.match(
            new RegExp("(^| )" + "token" + "=([^;]*)(;|$)")
          )[2]
        }
      },
      //省市区列表data
      addList: areasList,
      // 提交信息
      formValidate: {
        unitsName: "",
        unitsIntroduction: "",
        address: "",
        detailedAddress: "",
        unitsCertificate: "",
        unitsAuthorization: ""
      },
      //表单验证
      ruleValidate: {
        unitsName: [
          { required: true, message: "单位名称必填,不能为空", trigger: "blur" }
        ],
        unitsIntroduction: [
          { required: true, message: "单位简介必填,不能为空", trigger: "blur" }
        ],
        address: [
          {
            required: true,
            message: "省市区,不能为空",
            trigger: "change"
          }
        ],
        unitsCertificate: [
          {
            required: true,
            message: "单位法人证书必填,不能为空",
            trigger: "change"
          }
        ],
        unitsAuthorization: [
          {
            required: true,
            message: "单位授权书必填,不能为空",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
    /**
     * @function 省市区监听
     */
    address(value, selectedData) {
      let selectedDatas = selectedData.pop();
      this.formValidate.address = JSON.stringify({
        name: selectedDatas.__label,
        value: selectedDatas.__value
      });
    },
    unitsCertificate(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.unitsCertificate = res.data.url;
      }
    },
    unitsAuthorization(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.unitsAuthorization = res.data.url;
      }
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          government(this.formValidate).then(res => {
            if (res.data.status == "SUCCESS") {
              messages.success(
                `个人信息提交成功，等待审核,将在1-7个工作日内审核`
              );
              this.$router.push("/user/certify-index");
            }
          });
        } else {
          messages.warning(`必填项不能为空,请认真填写`);
        }
      });
    }
  }
};
</script>