<template>
  <div>
    <i-form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="180">
      <form-item label="请输入公司名称" prop="companyName" @submit.native.prevent>
        <Input v-model="formValidate.companyName" placeholder="公司名称" />
      </form-item>
      <form-item label="省/市/区" prop="address" @submit.native.prevent>
        <Cascader
          @on-change="address"
          :data="addList"
          style="width: 30%; float: left; margin-right:1%;"
        ></Cascader>
        <Input v-model="formValidate.detailedAddress" placeholder="详细地址" style="width: 69%;" />
      </form-item>
      <form-item label="请输入公司营业执证号" prop="licenseNumber" @submit.native.prevent>
        <Input v-model="formValidate.licenseNumber" placeholder="公司营业执证号" />
      </form-item>
      <form-item label="请输入授权人名称" prop="certigier" @submit.native.prevent>
        <Input v-model="formValidate.certigier" placeholder="授权人名称" />
      </form-item>
      <form-item label="企业营业执照 ( 照片 )" prop="licenseFile" @submit.native.prevent>
        <label>
          <Upload
            accept="image/*"
            :action="fileconfig.fileUrl"
            :headers="fileconfig.fileHead"
            name="file"
            :on-success="license_file"
            :show-upload-list="false"
          >
            <Button icon="ios-cloud-upload-outline">企业营业执照</Button>
            <div v-if="formValidate.licenseFile != ''">
              <p>{{formValidate.licenseFile}}</p>
              <p style="color: #2868d1;">*已添加,想更换直接上传新企业营业执照即可</p>
            </div>
          </Upload>
        </label>
      </form-item>
      <form-item label="企业授权书 ( PDF )" prop="authorization" @submit.native.prevent>
        <Upload
          accept=".pdf"
          :action="fileconfig.fileUrl"
          :headers="fileconfig.fileHead"
          name="file"
          :on-success="authorization"
          :show-upload-list="false"
        >
          <Button icon="ios-cloud-upload-outline">企业授权书 ( 格式: PDF )</Button>
          <div v-if="formValidate.authorization != ''">
            <p>{{formValidate.authorization}}</p>
            <p style="color: #2868d1;">*已添加,想更换直接上传新企业授权书即可</p>
          </div>
        </Upload>
        <a
          :href="`${baseUrl}/authorization/LetterOfAuthorization.doc`"
          style="color:#2868d1;"
        >&nbsp;&nbsp; *点击下载授权模板文件</a>
      </form-item>

      <form-item label="企业荣誉证书 ( 照片 )" @submit.native.prevent>
        <label>
          <Upload
            accept="image/*"
            :action="fileconfig.fileUrl"
            :headers="fileconfig.fileHead"
            name="file"
            :on-success="honorCertificate"
            :show-upload-list="false"
          >
            <Button icon="ios-cloud-upload-outline">企业荣誉证书</Button>
            <div v-if="formValidate.honorCertificate != ''">
              <p>{{formValidate.honorCertificate}}</p>
              <p style="color: #2868d1;">*已添加,想更换直接上传新企业荣誉证书即可</p>
            </div>
          </Upload>
        </label>
      </form-item>
      <form-item>
        <Button type="primary" @click="handleSubmit('formValidate')">提交企业认证</Button>
      </form-item>
    </i-form>
  </div>
</template>
<script>
import { fileUploadUrl, messages, validateNub, baseUrl } from "../../../utils";
import { company } from "../../../api/user";
import { areasList } from "../../../areas-list";
import { FormItem, Form, Input, Button, Upload, Cascader } from "iview";
export default {
  components: {
    [Form.name]: Form,
    [FormItem.name]: FormItem,
    [Button.name]: Button,
    [Input.name]: Input,
    [Upload.name]: Upload,
    [Cascader.name]: Cascader
  },
  data() {
    return {
      baseUrl,
      //省市区列表data
      addList: areasList,
      // 文件上传配置
      fileconfig: {
        fileUrl: fileUploadUrl,
        fileHead: {
          token: document.cookie.match(
            new RegExp("(^| )" + "token" + "=([^;]*)(;|$)")
          )[2]
        }
      },
      //提交信息
      formValidate: {
        companyName: "",
        address: "",
        detailedAddress: "",
        licenseNumber: "",
        certigier: "",
        licenseFile: "",
        authorization: "",
        honorCertificate: ""
      },
      //表单验证
      ruleValidate: {
        companyName: [
          { required: true, message: "公司名称必填,不能为空", trigger: "blur" }
        ],
        address: [
          {
            required: true,
            message: "省市区,不能为空",
            trigger: "change"
          }
        ],
        licenseNumber: [
          {
            trigger: "blur",
            message: "公司营业执证号必填",
            required: true
          }
        ],
        certigier: [
          {
            required: true,
            message: "授权人名称必填,不能为空",
            trigger: "blur"
          }
        ],
        licenseFile: [
          {
            required: true,
            message: "企业营业执照必填,不能为空",
            trigger: "change"
          }
        ],
        authorization: [
          {
            required: true,
            message: "企业授权书必填,不能为空",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
    /**
     * @function 省市区监听
     */
    address(value, selectedData) {
      let selectedDatas = selectedData.pop();
      this.formValidate.address = JSON.stringify({
        name: selectedDatas.__label,
        value: selectedDatas.__value
      });
    },
    /**
     * @function 企业营业执照
     */
    license_file(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.licenseFile = res.data.url;
      }
    },
    /**
     * @function 企业授权书
     */
    authorization(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.authorization = res.data.url;
      }
    },
    /**
     * @function 企业荣誉证书
     */
    honorCertificate(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.honorCertificate = res.data.url;
      }
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          company(this.formValidate).then(res => {
            if (res.data.status == "SUCCESS") {
              messages.success(
                `企业信息提交成功，等待审核,将在1-7个工作日内审核`
              );
              this.$router.push("/user/certify-index");
            }
          });
        } else {
          messages.warning(`必填项不能为空,请认真填写`);
        }
      });
    }
  }
};
</script>